   
.node{
    align-items: center;
    display: grid;
    height: 20px;
    width: 20px;
    border: black solid 0.5px;
}

.wall{
    background-color: black;
}

.start{
    background-color: chartreuse;
}

.end{
    background-color: red;
}

.path{
    background-color: yellow;
}

.vis{
    background-color: blue;
}